import Input from "@/components/input";
import Textarea from "@/components/text-area";
import { mapActions } from "vuex";
export default {
  name: "app-question-view",
  components: {
    "app-input": Input,
    "app-text-area": Textarea,
  },
  props: ["question"],
  data() {
    return {
      formData: {
        answer: "",
        name: localStorage.getItem("name"),
      },
    };
  },
  computed: {},
  mounted() {},
  methods: {
    ...mapActions(["postSolution"]),
    postAnswer() {
      this.postSolution({
        ...this.question,
        answers: [
          ...this.question.answers,
          { ...this.formData, timestamp: Date.now() },
        ],
      });

      this.formData = {
        answer: "",
        name: localStorage.getItem("name"),
      };
    },
  },
};
